body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

@font-face {
    font-family: primary;
    src: url(/crime-info/static/media/goboldBold.a213b3c5.otf);
}

@font-face {
    font-family: secondary;
    src: url(/crime-info/static/media/Florentia-Regular-trial.89a1d495.ttf);
}

h1 {
    font-family: primary;
    font-size: 48px;
    color: #F3E6C3;
    margin: 50px 25px 25px 50px;
    padding-top: 50px;
}

h2 {
    font-family: primary;
    color: #F3E6C3;
    margin: 50px 25px 25px 50px;
}

p {
    font-family: secondary;
    font-size: 24px;
    color: #F3E6C3;
}

.maxWidth {
    max-width: 1250px;
    margin: 0 auto;
}

.navBG {
    background: #876933;
    padding: 5px 0px 5px 0px;
}

.bar {
    height: 5px;
    margin-top: 0px;
    background: #F3E6C3;
}

ul {
    display: flex;
    justify-content: flex-end;
}

li {
    font-family: primary;
    font-size: 21px;
    width: 250px;
    display: flex;
    justify-content: space-between;
    margin-top: -25px;
}

a:link {
    text-decoration: none;
    color: #F3E6C3;
    font-family: primary;
}
  
a:visited {
    color: #F3E6C3;
    text-decoration: none;
}

a:hover {
    color: white;
    -webkit-animation-duration: 2.5s;
    animation-duration: 2.5s;
    -webkit-animation-fill-mode: both;
    animation-fill-mode: both;
    -webkit-animation-timing-function: linear;
    animation-timing-function: linear;
    animation-iteration-count: infinite;
    -webkit-animation-iteration-count: infinite;
    -webkit-animation-name: bounce;
    animation-name: bounce;
}

.animated {
    -webkit-animation-duration: 2.5s;
    animation-duration: 2.5s;
    -webkit-animation-fill-mode: both;
    animation-fill-mode: both;
    -webkit-animation-timing-function: linear;
    animation-timing-function: linear;
    animation-iteration-count: infinite;
    -webkit-animation-iteration-count: infinite;
  } 
  
  .bounce { 
    -webkit-animation-name: bounce;
    animation-name: bounce;
  }
  
  #logo {
      width: 175px;
      height: 100px;
      position: relative;
      top: 10px;
      left: 25px;
  }
    
  @-webkit-keyframes bounce {
    0%, 20%, 40%, 60%, 80%, 100% {-webkit-transform: translateY(0);}
    50% {-webkit-transform: translateY(-5px);}
  } 
  
  @keyframes bounce { 
    0%, 20%, 40%, 60%, 80%, 100% {-webkit-transform: translateY(0);transform: translateY(0);}
    50% {-webkit-transform: translateY(-5px);transform: translateY(-5px);}
  } 
.backImg {
    background-image: url(/crime-info/static/media/thief.32822ace.jpg);
    background-size: 100vw;
    width: 100vw;
    height: 100vh;
    margin-top: -50px;
    z-index: -1;
}
.canvasjs-chart-toolbar {
  visibility: hidden;
}
